/**
 * Plasma Slider
 * Company: Aazz Tech
 * Develoepr: Syed Galib Ahmed
 * Version: 1.0
 * Initial Release: 1 February, 2020
 * */
.plasmaSlider {
  position: relative;
  background-color: gainsboro;
}

.plasmaSlider__body {
  padding-top: 52.25%;
  position: relative;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
  background-color: gainsboro;
}

.plasmaSlider__controlRight,
.plasmaSlider__controlLeft {
  position: absolute;
  top: 50%;
  z-index: 99;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

/*rtl:begin:ignore*/
.plasmaSlider__controlRight {
  left: unset;
  right: 15px;
}

.plasmaSlider__controlLeft {
  right: unset;
  left: 15px;
}

/*rtl:end:ignore*/
.plasmaSlider__arrowBtn {
  margin: 0;
  padding: 0;
  position: relative;
  cursor: pointer;
  border: none;
  color: #fff;
  font-size: 13px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  display: block;
  background-color: rgba(0, 0, 0, 0.2);
}

.plasmaSlider__arrowBtn:hover,
.plasmaSlider__arrowBtn:active,
.plasmaSlider__arrowBtn:visited,
.plasmaSlider__arrowBtn:focus {
  border: none;
  outline: 0;
  background-color: rgba(0, 0, 0, 0.2);
}

.plasmaSlider__icon {
  color: #fff;
  font-size: 13px;
}

.plasmaSlider__arrowBtn .plasmaSliderIcon {
  width: 100%;
  height: 100%;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 12px;
}

.plasmaSlider__contents {
  overflow: hidden;
  z-index: 88;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.plasmaSlider__contentsWrap {
  position: relative;
  -webkit-transition: all ease-in-out 300ms;
  -o-transition: all ease-in-out 300ms;
  transition: all ease-in-out 300ms;
  height: 100%;
  width: 0%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  transition: all ease-in-out 300ms;
  left: 0;
}

.plasmaSlider__bg {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: gainsboro;
}

.plasmaSlider__bgImg,
.plasmaSlider__bgImgBlur {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.plasmaSlider__bgImgBlur {
  z-index: 55;
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  transform: scale(1.5);
  -o-object-fit: cover;
  object-fit: cover;
  -webkit-filter: blur(10px);
  filter: blur(10px);
  -webkit-animation: plasmaSliderZoomIn linear infinite 20s;
  animation: plasmaSliderZoomIn linear infinite 20s;
}

.plasmaSlider__bgImg {
  z-index: 88;
